import {authThunk} from "./auth-reducer";

const SET_INIT = 'SET_INIT';
const SET_BURGER = 'SET_BURGER';



let initialState = {
    init: false,
    burger: false
};

const appReducer = (state = initialState, action) => {

    switch (action.type) {
        case SET_INIT:
            return {
                ...state,
               init: true
            }
        case SET_BURGER:
            return {
                ...state,
                burger: action.burger
            }
        default:
            return state;
    }
}


export const InitSucces = () =>  ({type: SET_INIT})
export const openBurger = (burger) =>  ({type: SET_BURGER, burger})

export const InitApp = () => (dispatch) =>  {
    let promise = dispatch(authThunk()) //—á—Ç–æ–±—ã —ç—Ç–æ —Å—Ä–∞–±–æ—Ç–∞–ª–æ, –≤ authThunk –¥–æ–ª–∂–µ–Ω –±—ã—Ç—å return –ø–µ—Ä–µ–¥ –∑–∞–ø—Ä–æ—Å–æ–º
    //let promise2 = dispatch(—á—Ç–æ-—Ç–æ())
    //let promise3 = dispatch(—á—Ç–æ-—Ç–æ())


    Promise.all([promise]).then(()=>{         //–µ—Å–ª–∏ –Ω—É–∂–Ω–æ –¥–∏—Å–ø–∞—Ç—á–∏—Ç—å –Ω–µ—Å–∫–æ–ª—å–∫–æ —ç–∫—à–µ–Ω–æ–≤ –¥–æ –∏–Ω–∏—Ç–∏—Ü–∞–ª–∏–∑–∞—Ü–∏–∏, —Ç–æ –∏—Ö –ø—Ä–æ—Å—Ç–æ –º–æ–∂–Ω–æ –¥–æ–±–∞–≤–∏—Ç—å –≤ –º–∞—Å—Å–∏–≤[promise, promise2, ....]
        dispatch(InitSucces())                      //–±–ª–∞–≥–æ–¥–∞—Ä—è —ç—Ç–∏–º —Å—Ç—Ä–æ–∫–∞–º, –ø—Ä–∏–ª–æ–∂–µ–Ω–∏–µ–º –Ω–µ –ø—Ä–æ–∏–Ω–∏—Ç–∏–∞–ª–∏–∑–∏—Ä—É–µ—Ç—Å—è, –ø–æ–∫–∞ –Ω–µ –ø—Ä–∏–¥—ë—Ç –æ—Ç–≤–µ—Ç –æ—Ç –≤—Å–µ—Ö –∑–∞–ø—Ä–æ—Å–æ–≤ –≤ –¥–∏—Å–ø–∞—Ç—á–∞—Ö –¥–æ InitSucces
    })                                              //—ç—Ç–æ –¥–∞—ë—Ç –≤–æ–∑–º–æ–∂–Ω–æ—Å—Ç—å –ø–æ–∫–∞–∑–∞—Ç—å –ø—Ä–µ–ª–æ—É–¥–µ—Ä –≤–º–µ—Å—Ç–æ –≤—Å—è–∫–∏—Ö –º–æ—Ä–≥–∞–Ω–∏–π –ø–æ–∫–∞ –∫–æ–Ω—Ç–µ–Ω—Ç –Ω–µ –∑–∞–≥—Ä—É–∑–∏–ª—Å—è
}






export default appReducer;