import * as serviceWorker from './serviceWorker';
import store from "./redux/redux-store";
import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import {BrowserRouter, HashRouter} from "react-router-dom"; //BrowserRouter –æ—Ç–æ–±—Ä–∞–∂–∞–µ—Ç —Å—Å—ã–ª–∫–∏ –±–µ–∑ #, HashRouter –æ—Ç–æ–±—Ä–∞–∂–∞–µ—Ç —Å—Å—ã–ª–∫–∏ —Å –∑–Ω–∞–∫–æ–º #, —ç—Ç–æ –Ω—É–∂–Ω–æ –¥–ª—è –∫–æ—Ä—Ä–µ–∫—Ç–Ω–æ–π —Ä–∞–±–æ—Ç—ã github.pages
import {Provider} from "react-redux"; //<Provider />–¥–µ–ª–∞–µ—Ç Redux store –¥–æ—Å—Ç—É–ø–Ω—ã–º –¥–ª—è –ª—é–±—ã—Ö –≤–ª–æ–∂–µ–Ω–Ω—ã—Ö –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–æ–≤, –∫–æ—Ç–æ—Ä—ã–µ –±—ã–ª–∏ –∑–∞–∫–ª—é—á–µ–Ω—ã –≤ connect()—Ñ—É–Ω–∫—Ü–∏—é.

//–≤ <Provider> –º–æ–∂–Ω–æ –∑–∞–∫–∏–Ω—É—Ç—å –∫—Ä–æ–º–µ store –ª—é–±–æ–π –µ–ª–µ–º–µ–Ω—Ç –Ω–µ–æ–±—Ö–æ–¥–∏–º—ã–π –≤—Å–µ–º—É –ø—Ä–∏–ª–æ–∂–µ–Ω–∏—é

